import { Alert, Button, Input, Select } from "@windmill/react-ui";
import { useTranslation } from "react-i18next";

import Error from "components/form/Error";
import spinnerLoadingImage from "assets/img/spinner.gif";
import InputAreaTwo from "components/form/InputAreaTwo";
import PageTitle from "components/Typography/pageTitleButton";
import useSettingSubmit from "hooks/useAddEventSubmit";
import Uploader from "components/image-uploader/Uploader";
import {
  getAllCommonActivityList,
  getAllCommonActivityListByproperyID,
  getAllCommonBuffectsList,
  getAllCommonProperyList,
} from "services/Api";
import { useEffect, useState } from "react";
import moment from "moment";
import DatePanel from "react-multi-date-picker/plugins/date_panel";
import DatePicker from "react-multi-date-picker";
import LabelArea from "components/form/LabelArea";

const Setting = () => {
  const {
    errors,
    watch,
    register,
    handleSubmit,
    onSubmit,
    imageUrl,
    resetBtn,
    setImageUrl,
    isSave,
    isSubmitting,
    setDates,
    dates,
  } = useSettingSubmit();
  const [propertyList, setpropertyList] = useState([]);
  const [Activitylist, setActivitylist] = useState([]);
  const [Buffectslist, setBuffectslist] = useState([]);
  const [value, onChange] = useState(new Date());
  const [loading, setLoading] = useState(false);
  const format = "MM/DD/YYYY";
  const { t } = useTranslation();
  useEffect(() => {
    getPropertyList();
    getBuffectsList();
    getActivityList();
  }, []);

  const getPropertyList = async () => {
    const PropertyList = await getAllCommonProperyList();
    console.log(
      PropertyList?.data?.data?.propertyList,
      "getAllcommonProperyList"
    );
    setpropertyList(PropertyList?.data?.data?.propertyList);
    setLoading(false);
  };
  const getActivityList = async (id) => {
    const ActivityData = await getAllCommonActivityListByproperyID(id);
    console.log(ActivityData?.data?.data?.activity, "getAllCommonActivityList");
    setActivitylist(ActivityData?.data?.data?.activity);
    setLoading(false);
  };

  const setDates1 = (e) => {
    console.log(e, "eee");
    setDates(e?.map((date) => date?.format("MM/DD/YYYY")));
  };

  const handleSelectChange = (e) => {
    getActivityList(e.target.value);
  };
  const getBuffectsList = async () => {
    const BuffectsData = await getAllCommonBuffectsList();
    console.log(BuffectsData?.data?.data?.buffets, "BuffectsData");
    setBuffectslist(BuffectsData?.data?.data?.buffets);
    setLoading(false);
  };
  const containsSpecialCharsOrNumbers = (value) => {
    return /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?0-9]/.test(value);
  };
  const phonePattern = /^[0-9]{10}$/;
  const currentDate = new Date().toISOString().split("T")[0];
  const checkInWatch = watch("cinDate");
  console.log(currentDate, "currentDate");
  return (
    <>
      <PageTitle resetBtn={resetBtn}>{t("Add Events")}</PageTitle>

      <div className="container p-6 mx-auto bg-white  dark:bg-gray-800 dark:text-gray-200 rounded-lg">
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className="grid grid-cols-12 font-sans">
            <div className="col-span-12 md:col-span-12 lg:col-span-12 mr-3 ">
              <div className="flex-grow scrollbar-hide w-full max-h-full pb-0">
                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Select Property")}
                    </label>

                    <div className="sm:col-span-3">
                      <Select
                        {...register(`propertyID`, {
                          required: "Select Property is required !",
                        })}
                        onChange={(e) => handleSelectChange(e)}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        <option value="" defaultValue hidden>
                          {t("Select Property")}
                        </option>
                        {propertyList?.map((pro) => (
                          <option value={pro.id}>{pro.name}</option>
                        ))}
                      </Select>
                      <Error errorName={errors.propertyID} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Name")}
                    </label>
                    <div className="sm:col-span-3">
                      <Input
                        {...register("name", {
                          required: "Event name required!",
                          validate: (value) =>
                            !containsSpecialCharsOrNumbers(value) ||
                            "Name cannot contain special characters or numbers",
                        })}
                        label={t("Name")}
                        name="name"
                        type="text"
                        placeholder={t("Enter Name")}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      />
                      <Error errorName={errors.name} pos />
                    </div>
                  </div>
                </div>

                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Check In Date")}
                    </label>
                    <div className="flex1">
                      <div className="sm:col-span-5 w-260">
                        <InputAreaTwo
                          register={register}
                          label={t("Check in Date")}
                          name="cinDate"
                          type="Date"
                          required
                          placeholder={t("Check In Date")}
                          min={currentDate}
                        />
                        <Error errorName={errors.cinDate} pos />
                      </div>
                      <div
                        className="sm:col-span-5"
                        style={{ marginTop: "-25px" }}
                      >
                        <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                          {t("Check In Time")}
                        </label>
                        <div className="sm:col-span-5 w-260">
                          <InputAreaTwo
                            register={register}
                            label={t("Check in Time")}
                            name="CheckinTime"
                            type="time"
                            placeholder={t("Check In Time")}
                          />
                          <Error errorName={errors.CheckinTime} pos />
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Check Out Date")}
                    </label>
                    <div className="flex1">
                      <div className="sm:col-span-5 w-260">
                        <InputAreaTwo
                          register={register}
                          label={t("Check Out Date")}
                          name="CheckOutDate"
                          type="Date"
                          required
                          placeholder={t("Check Out Date")}
                          min={moment(checkInWatch).format("YYYY-MM-DD")}
                        />
                        <Error errorName={errors.CheckOutDate} pos />
                      </div>
                      <div
                        className="sm:col-span-5"
                        style={{ marginTop: "-25px" }}
                      >
                        <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                          {t("Check Out Time")}
                        </label>
                        <div className="sm:col-span-5 w-260">
                          <InputAreaTwo
                            register={register}
                            label={t("Check Out Time")}
                            name="CheckinOutTime"
                            type="time"
                            placeholder={t("Check out Time")}
                          />
                          <Error errorName={errors.CheckinOutTime} pos />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Event Details")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Event Details")}
                        name="EventDetails"
                        required
                        type="text"
                        placeholder={t("Enter Event Details")}
                      />
                      <Error errorName={errors.EventDetails} pos />
                    </div>
                  </div>

                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Address Details")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("AddressDetails")}
                        name="AddressDetails"
                        type="text"
                        placeholder={t("Enter Address Details")}
                      />
                      <Error errorName={errors.AddressDetails} pos />
                    </div>
                  </div>
                </div>

                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-6">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Select Activity")}
                    </label>

                    <div className="sm:col-span-3">
                      <Select
                        {...register(`ActivityID`, {
                          required: false,
                        })}
                        multiple
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        <option value="" defaultValue hidden>
                          {t("Select Activity")}
                        </option>
                        {Activitylist?.map((pro) => (
                          <option value={pro.id}>{pro.name}</option>
                        ))}
                      </Select>
                      <Error errorName={errors.ActivityID} pos />
                    </div>
                  </div>
                </div>

                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Select Buffets")}
                    </label>

                    <div className="sm:col-span-3">
                      <Select
                        {...register(`BuffetsId`, {
                          // required: "Select Buffets is required !",
                        })}
                        multiple
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        <option value="" defaultValue hidden>
                          {t("Select Buffets")}
                        </option>
                        {Buffectslist?.map((pro) => (
                          <option value={pro.id}>{pro.name}</option>
                        ))}
                      </Select>
                      <Error errorName={errors.BuffetsId} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-3">
                    <div className="grid md:grid-cols-6 mt-5 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                      <div className="col-span-4 sm:col-span-3">
                        <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                          {t("Total Availability")}
                        </label>
                        <div className="sm:col-span-2">
                          <Input
                            {...register("TotalAvailblity", {
                              required: "Availability required!",
                              pattern: {
                                value: /^[0-9]*$/,
                                message: "Please enter a valid number !.",
                              },
                            })}
                            label={t("Total Availability")}
                            name="TotalAvailblity"
                            type="text"
                            placeholder={t("Total Availability")}
                            className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                          />
                          <Error errorName={errors.TotalAvailblity} pos />
                        </div>
                      </div>
                      <div className="col-span-4 sm:col-span-3">
                        <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                          {t("Child Price")}
                        </label>
                        <div className="sm:col-span-3">
                          <Input
                            {...register("ChildPrice", {
                              required: "Child Price required!",
                              pattern: {
                                value: /^[0-9]*$/,
                                message: "Please enter a valid number !.",
                              },
                            })}
                            label={t("ChildPrice")}
                            name="ChildPrice"
                            type="text"
                            placeholder={t("Child Price")}
                            className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                          />
                          <Error errorName={errors.ChildPrice} pos />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-3">
                    <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                      <div className="col-span-4 sm:col-span-6">
                        <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                          {"Adult Amount"}
                        </label>
                        <Input
                          {...register("AdultAmount", {
                            required: "Adult Amount required!",
                            pattern: {
                              value: /^[0-9]*$/,
                              message: "Please enter a valid number !.",
                            },
                          })}
                          label={"Amount"}
                          name="AdultAmount"
                          type="text"
                          placeholder={t("Enter Amount")}
                          className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                        />
                        <Error errorName={errors.AdultAmount} />
                      </div>
                    </div>
                  </div>
                  <div className="sm:col-span-3" style={{ marginTop: "-28px" }}>
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Organiser By")}
                    </label>
                    <div className="sm:col-span-3">
                      <Select
                        {...register(`OrganiserBy`, {
                          required: "Select Organiser is required !",
                        })}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        <option value="" defaultValue hidden>
                          {t("Select Organiser")}
                        </option>
                        <option value="wild_valley">Wild Valley</option>
                        <option value="other">Other</option>
                      </Select>
                      <Error errorName={errors.OrganiserBy} pos />
                    </div>
                  </div>
                </div>

                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Organiser Name")}
                    </label>
                    <div className="sm:col-span-3">
                      <Input
                        {...register("NameOrganiser", {
                          required: "Name Organiser required!",
                          validate: (value) =>
                            !containsSpecialCharsOrNumbers(value) ||
                            "Name cannot contain special characters or numbers",
                        })}
                        label={t("Organiser name")}
                        name="NameOrganiser"
                        type="text"
                        placeholder={t("Organiser Name")}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      />
                      <Error errorName={errors.NameOrganiser} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Organiser Email")}
                    </label>
                    <div className="sm:col-span-3">
                      <Input
                        {...register("OrganiserEmail", {
                          required: "Organiser Email required!",
                          pattern: {
                            value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                            message: "Invalid email address",
                          },
                        })}
                        label={t("Organiseremail")}
                        name="OrganiserEmail"
                        type="Email"
                        // disabled={true}
                        placeholder={t("Organiser Email")}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      />
                      <Error errorName={errors.OrganiserEmail} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Organiser Phone Number")}
                    </label>
                    <div className="sm:col-span-3">
                      <Input
                        {...register("OrganiserPhone", {
                          required: "Organiser Phone required!",
                          pattern: {
                            value: phonePattern,
                            message: "Invalid phone number format!",
                          },
                        })}
                        label={t("Organiser Phone")}
                        name="OrganiserPhone"
                        type="text"
                        maxLength={10}
                        placeholder={t("Organiser Phone Number")}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      />
                      <Error errorName={errors.OrganiserPhone} pos />
                    </div>
                  </div>
                </div>

                {/* <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-6">
                    <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                      <div className="col-span-4 sm:col-span-6">
                        <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                          {"Block Dates"}
                        </label>
                        <DatePicker
                          value={dates}
                          //   onChange={setDates}
                          onChange={setDates1}
                          multiple
                          sort
                          minDate={new Date()}
                          format={format}
                          placeholder="Please Enter Blocks Date"
                          calendarPosition="bottom-center"
                          plugins={[<DatePanel />]}
                        />
                      </div>
                    </div>
                  </div>
                </div> */}

                <div className="sm:col-span-3">
                  <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                    {t("Photos")}
                  </label>
                  <div className="grid md:grid-cols-4 items-center sm:grid-cols-4 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-4">
                    <div>
                      <Uploader
                        product
                        folder="product"
                        imageUrl={imageUrl}
                        setImageUrl={setImageUrl}
                      />
                      <Alert className="sm12" type="info">
                        Image should be 600x600px and Image Size should be
                        less than 5mb
                      </Alert>
                    </div>
                    {/* <Error errorName={errors.imageUrl} pos /> */}
                  </div>
                </div>

                <div className="flex flex-row-reverse pb-6">
                  <Button
                    type="button"
                    className="h-12 px-8"
                    onClick={() => resetBtn()}
                    style={{
                      position: "relative",
                      right: "22em",
                      backgroundColor: "gainsboro",
                      color: "red",
                    }}
                  >
                    Cancel
                  </Button>
                  {isSubmitting ? (
                    <Button disabled={true} type="button" className="h-12">
                      <img
                        src={spinnerLoadingImage}
                        alt="Loading"
                        width={20}
                        height={10}
                      />{" "}
                      <span className="font-serif ml-2 font-light">
                        Processing
                      </span>
                    </Button>
                  ) : (
                    <Button type="submit" className="h-12 px-8">
                      {" "}
                      {isSave ? "Save" : "Add Events"}
                    </Button>
                  )}
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    </>
  );
};
export default Setting;
